.flexWrapper {
    display: flex;
    flex-flow: row nowrap;
}

.singleChart {
    width: 100%;
    justify-content: space-around ;
}

.circularChart {
    display: block;
    margin: 10px auto;
    max-width: 100%;
    max-height: 250px;
}

.circleBg {
    fill: none;
    stroke: #eee;
    stroke-width: 3.8;
}

.circle {
    fill: none;
    stroke-width: 2.8;
    stroke-linecap: round;
    animation: progress 1s ease-out forwards;
}

@keyframes progress {
    0% {
        stroke-dasharray: 100;
    }
}

.circularChart.orange .circle {
    stroke: #ff9f00;
}

.circularChart.green .circle {
    stroke: #4CC790;
}

.circularChart.blue .circle {
    stroke: #3c9ee5;
}

.percentage {
    fill: #666;
    font-family: sans-serif;
    font-size: 0.75em;
    text-anchor: middle;
    font-weight:500;
}